@import './variables.css';

/* ===== Layout ===== */

.login-page {
    min-height: 100vh;
    display: flex;
    background: var(--bg-gradient);
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Ubuntu, sans-serif;
}

/* ===== Card ===== */

.login-container {
    width: 100%;
    max-width: 440px;
    background: var(--bg-color);
    padding: 40px 32px;
    display: flex;
    flex-direction: column;
    box-shadow: var(--shadow-lg);
    z-index: 2;
}

/* ===== Right Side ===== */

.login-background {
    flex: 1;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 40px;
    color: var(--text-light);
}

/* ===== Decorative Shapes ===== */

.background-shapes {
    position: absolute;
    inset: 0;
    overflow: hidden;
}

.shape {
    position: absolute;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.12);
}

.shape-1 {
    width: 320px;
    height: 320px;
    top: -160px;
    right: -160px;
}

.shape-2 {
    width: 220px;
    height: 220px;
    bottom: -110px;
    left: -110px;
}

.shape-3 {
    width: 160px;
    height: 160px;
    top: 45%;
    left: 35%;
}

/* ===== Background Text ===== */

.background-info {
    position: relative;
    max-width: 480px;
    z-index: 1;
}

.background-info h2 {
    font-size: 2.4rem;
    margin-bottom: 28px;
}

.background-info ul {
    list-style: none;
    padding: 0;
}

.background-info li {
    display: flex;
    align-items: center;
    gap: 12px;
    font-size: 1.05rem;
    margin-bottom: 14px;
}

.background-info i {
    color: var(--primary-color);
}

/* ===== Header ===== */

.login-header {
    text-align: center;
    margin-bottom: 32px;
}

.logo {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;
}

.logo i {
    font-size: 2.6rem;
    color: var(--primary-color);
}

.logo h1 {
    font-size: 2rem;
    color: var(--text-primary);
}

.subtitle {
    margin-top: 6px;
    color: var(--text-secondary);
}

/* ===== Error ===== */

.error-message {
    background: rgba(255, 68, 68, 0.12);
    border: 1px solid var(--error-color);
    color: var(--error-color);
    padding: 10px 14px;
    border-radius: var(--border-radius-md);
    margin-bottom: 18px;
}

/* ===== Form ===== */

.login-form {
    display: flex;
    flex-direction: column;
    gap: 5px;
}
.form-group {
    display: flex;
    flex-direction: column;
    gap: 18px;
}
.form-group label {
    font-size: 0.85rem;
    font-weight: 600;
    color: var(--text-primary);
}

.form-group input {
    padding: 12px 14px;
    border-radius: var(--border-radius-md);
    border: 1px solid var(--border-color);
    background: var(--bg-secondary);
    color: var(--text-primary);
    font-size: 0.95rem;
    transition: var(--transition-fast);
}

.form-group input:focus {
    outline: none;
    border-color: var(--primary-color);
    box-shadow: 0 0 0 2px var(--shadow-primary);
}

/* ===== Options ===== */

.form-options {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 0.85rem;
}

.checkbox-label {
    display: flex;
    gap: 8px;
    color: var(--text-secondary);
}

.forgot-password {
    color: var(--primary-color);
    text-decoration: none;
}

.forgot-password:hover {
    text-decoration: underline;
}

/* ===== Button ===== */

.login-button {
    margin-top: 10px;
    padding: 14px;
    border-radius: var(--border-radius-md);
    background: var(--primary-color);
    color: var(--text-light);
    font-weight: 600;
    border: none;
    cursor: pointer;
    transition: var(--transition-fast);
}

.login-button:hover:not(:disabled) {
    background: var(--primary-dark);
    box-shadow: var(--shadow-primary-md);
}

.login-button:disabled {
    opacity: 0.6;
}

/* ===== Demo ===== */

.demo-login-section {
    margin-top: 10px;
}

.demo-buttons {
    display: flex;
    gap: 10px;
}

.demo-button {
    flex: 1;
    padding: 10px;
    border-radius: var(--border-radius-md);
    border: 1px solid var(--border-color);
    background: var(--bg-secondary);
    cursor: pointer;
    transition: var(--transition-fast);
}

.demo-button:hover {
    border-color: var(--primary-color);
    background: var(--primary-light);
}

/* ===== Footer ===== */

.login-footer {
    margin-top: auto;
    padding-top: 16px;
    border-top: 1px solid var(--border-light);
    font-size: 0.8rem;
    text-align: center;
    color: var(--text-tertiary);
}

/* ===== Responsive ===== */

@media (max-width: 768px) {
    .login-page {
        flex-direction: column;
    }

    .login-background {
        display: none;
    }

    .login-container {
        max-width: 100%;
    }
}
