.profile-overlay {
    position: fixed;
    inset: 0;
    background: rgba(0,0,0,.5);
    z-index: 100;
}

.profile-drawer {
    width: 320px;
    height: 100%;
    background: var(--bg);
    padding: 24px;
    animation: slideIn .25s ease;
}

@keyframes slideIn {
    from { transform: translateX(-100%); }
    to { transform: translateX(0); }
}

.profile-avatar {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    margin-bottom: 16px;
}

.edit-btn {
    margin-top: 20px;
}

.edit-form input {
    width: 100%;
    margin-bottom: 8px;
}
