:root {
    /* Основные цвета мессенджера */
    --primary-color: #9a39ca;
    --primary-dark: #712493;
    --primary-light: #e6f7ff;

    /* Фоновые цвета */
    --bg-color: #ffffff;
    --bg-secondary: #f0f2f5;
    --bg-tertiary: #f8f9fa;
    --bg-gradient: linear-gradient(135deg, #393787 0%, #764ba2 100%);
    --bg-chat: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);

    /* Цвета текста */
    --text-primary: #333333;
    --text-secondary: #666666;
    --text-tertiary: #999999;
    --text-light: #ffffff;
    --text-muted: #54656f;

    /* Цвета сообщений */
    --message-sent-bg: var(--primary-color);
    --message-sent-text: var(--text-light);
    --message-received-bg: #ffffff;
    --message-received-text: var(--text-primary);

    /* Цвета элементов интерфейса */
    --border-color: #e6e6e6;
    --border-light: #f0f0f0;
    --border-dark: #ddd;
    --shadow-color: rgba(0, 0, 0, 0.1);
    --shadow-primary: rgba(183, 0, 255, 0.4);

    /* Статусные цвета */
    --online-color: var(--unread-badge);
    --offline-color: #999999;
    --error-color: #ff4444;
    --success-color: #00C851;
    --warning-color: #ffbb33;

    /* Индикаторы */
    --unread-badge: var(--primary-color);
    --active-indicator: var(--primary-color);

    /* Размеры */
    --border-radius-sm: 5px;
    --border-radius-md: 10px;
    --border-radius-lg: 20px;
    --border-radius-circle: 50%;

    /* Тени */
    --shadow-sm: 0 2px 5px var(--shadow-color);
    --shadow-md: 0 4px 20px var(--shadow-color);
    --shadow-lg: 0 20px 60px rgba(0, 0, 0, 0.3);
    --shadow-primary-md: 0 4px 20px var(--shadow-primary);

    /* Анимации */
    --transition-fast: 0.2s ease;
    --transition-normal: 0.3s ease;
    --transition-slow: 0.5s ease;
}

/* Темная тема (опционально) */
[data-theme="dark"] {
    --primary-color: #583070;
    --primary-dark: #4d1765;
    --primary-light: rgba(42, 158, 246, 0.1);

    --bg-color: #0d141c;
    --bg-secondary: #1a2530;
    --bg-tertiary: #222e3c;
    --bg-gradient: linear-gradient(135deg, #1a2530 0%, #0d141c 100%);
    --bg-chat: #131a22;

    --text-primary: #ffffff;
    --text-secondary: #b0b7c3;
    --text-tertiary: #8a94a6;
    --text-light: #ffffff;
    --text-muted: #8a94a6;

    --message-received-bg: #222e3c;
    --message-received-text: #ffffff;

    --border-color: #2d3746;
    --border-light: #3a4555;
    --border-dark: #2d3746;
    --shadow-color: rgba(0, 0, 0, 0.3);
}