@import 'variables.css';

.sidebar {
    width: 30%;
    min-width: 320px;
    display: flex;
    flex-direction: column;
    border-right: 1px solid var(--border-color);
    background: var(--bg-tertiary);
}

.sidebar-header {
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: var(--primary-color);
    color: var(--text-light);
}

.user-info {
    display: flex;
    align-items: center;
    gap: 12px;
}

.user-avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
    border: 2px solid var(--text-light);
}

.user-avatar img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.user-name {
    font-weight: 600;
    font-size: 16px;
}

.sidebar-actions {
    display: flex;
    gap: 10px;
    align-items: center;
}

.icon-btn {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    border: none;
    background: rgba(255, 255, 255, 0.2);
    color: var(--text-light);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: all var(--transition-fast);
}

.icon-btn:hover {
    background: rgba(255, 255, 255, 0.3);
    transform: scale(1.05);
}

.search-box {
    padding: 15px 20px;
    background: var(--bg-color);
    border-bottom: 1px solid var(--border-color);
    position: relative;
}

.search-box input {
    width: 100%;
    padding: 10px 10px 10px 40px;
    border: 1px solid var(--border-dark);
    border-radius: var(--border-radius-lg);
    background: var(--bg-secondary);
    color: var(--text-primary);
}

.search-box input:focus {
    border-color: var(--primary-color);
}

.search-box i {
    position: absolute;
    left: 12px;
    top: 50%;
    transform: translateY(-50%);
    color: var(--text-tertiary);
}

.side-chats-list {
    flex: 1;
    overflow-y: auto;
    background: var(--bg-color);
}

.chat-item {
    display: flex;
    padding: 15px 20px;
    cursor: pointer;
    border-bottom: 1px solid var(--border-light);
    transition: all var(--transition-fast);
}

.chat-item:hover {
    background: var(--bg-secondary);
}

.chat-item.active {
    background: var(--primary-light);
    border-left: 4px solid var(--active-indicator);
}

.side-chat-avatar {
    position: relative;
    margin-right: 15px;
}

.side-chat-avatar img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
}

.online-status {
    position: absolute;
    bottom: 2px;
    right: 2px;
    width: 12px;
    height: 12px;
    background: var(--online-color);
    border: 2px solid var(--bg-color);
    border-radius: 50%;
}

.side-chat-info {
    flex: 1;
    min-width: 0;
}

.side-chat-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;
}

.side-chat-header h4 {
    font-size: 16px;
    font-weight: 600;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.side-chat-time {
    font-size: 12px;
    color: var(--text-tertiary);
}

.side-chat-preview {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.side-chat-preview p {
    font-size: 14px;
    color: var(--text-secondary);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-right: 10px;
}

.unread-badge {
    background: var(--unread-badge);
    color: var(--text-light);
    font-size: 12px;
    font-weight: 600;
    min-width: 20px;
    height: 20px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 6px;
}

.side-chats-list::-webkit-scrollbar {
    width: 6px;
}

.side-chats-list::-webkit-scrollbar-track {
    background: var(--bg-secondary);
}

.side-chats-list::-webkit-scrollbar-thumb {
    background: var(--text-tertiary);
    border-radius: var(--border-radius-sm);
}

.side-chats-list::-webkit-scrollbar-thumb:hover {
    background: var(--text-secondary);
}

/* Поиск */
.search-results {
    background: var(--bg-secondary);
}
