@import 'variables.css';

.chat-window {
    flex: 1;
    display: flex;
    flex-direction: column;
    background: var(--bg-secondary);
}

.chat-window.empty {
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--bg-chat);
}

.empty-chat {
    text-align: center;
    color: var(--text-muted);
    max-width: 400px;
    padding: 40px;
}

.empty-chat i {
    font-size: 80px;
    color: var(--primary-color);
    margin-bottom: 20px;
    opacity: 0.7;
}

.empty-chat h2 {
    font-size: 28px;
    font-weight: 300;
    margin-bottom: 15px;
    color: var(--text-primary);
}

.empty-chat p {
    font-size: 16px;
    color: var(--text-secondary);
}

.chat-header {
    padding: 15px 20px;
    background: var(--primary-color);
    color: var(--text-light);
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: var(--shadow-sm);
}

.chat-contact {
    display: flex;
    align-items: center;
    gap: 15px;
}

.contact-avatar {
    position: relative;
}

.contact-avatar img {
    width: 40px;
    height: 40px;
    border-radius: var(--border-radius-circle);
    object-fit: cover;
    border: 2px solid rgba(255, 255, 255, 0.5);
}

.online-dot {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 10px;
    height: 10px;
    background: var(--online-color);
    border: 2px solid var(--primary-color);
    border-radius: var(--border-radius-circle);
}

.contact-info h3 {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 3px;
}

.contact-info p {
    font-size: 13px;
    opacity: 0.9;
}

.chat-actions {
    display: flex;
    gap: 15px;
}

.chat-actions .icon-btn {
    background: rgba(255, 255, 255, 0.2);
    color: var(--text-light);
}

.chat-actions .icon-btn:hover {
    background: rgba(255, 255, 255, 0.3);
}

.messages-container {
    flex: 1;
    padding: 20px;
    overflow-y: auto;
    background-image: url('https://wallpapercave.com/wp/wp4410743.png');
    background-size: cover;
    background-attachment: fixed;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.message {
    max-width: 70%;
    display: flex;
    flex-direction: column;
    animation: fadeIn var(--transition-normal);
}

@keyframes fadeIn {
    from { opacity: 0; transform: translateY(10px); }
    to { opacity: 1; transform: translateY(0); }
}

.message.sent {
    align-self: flex-end;
}

.message.received {
    align-self: flex-start;
}

.message-content {
    position: relative;
    padding: 12px 16px;
    border-radius: var(--border-radius-lg);
    margin-bottom: 5px;
    word-wrap: break-word;
    max-width: 100%;
}

.message.sent .message-content {
    background: var(--message-sent-bg);
    color: var(--message-sent-text);
    border-bottom-right-radius: var(--border-radius-sm);
}

.message.received .message-content {
    background: var(--message-received-bg);
    color: var(--message-received-text);
    border-bottom-left-radius: var(--border-radius-sm);
    box-shadow: var(--shadow-sm);
}

.message-time {
    font-size: 11px;
    color: rgba(255, 255, 255, 0.8);
    margin-left: 10px;
    float: right;
}

.message.received .message-time {
    color: var(--text-tertiary);
}

.read-icon {
    margin-left: 5px;
    color: #4fc3f7;
}

.message-media {
    max-width: 300px;
    max-height: 300px;
    border-radius: var(--border-radius-md);
    overflow: hidden;
    cursor: pointer;
}

.message-media img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform var(--transition-normal);
}

.message-media img:hover {
    transform: scale(1.05);
}

.message-input-area {
    padding: 15px 20px;
    background: var(--bg-color);
    display: flex;
    align-items: center;
    gap: 15px;
    border-top: 1px solid var(--border-color);
}

.input-actions {
    display: flex;
    gap: 10px;
}

.input-actions .icon-btn {
    background: transparent;
    color: var(--text-secondary);
    font-size: 18px;
}

.input-actions .icon-btn:hover {
    color: var(--primary-color);
    transform: scale(1.05);
}

.message-input-wrapper {
    flex: 1;
    position: relative;
    background: var(--bg-secondary);
    border-radius: var(--border-radius-lg);
    padding: 5px 10px;
    display: flex;
    align-items: center;
}

.message-input-wrapper textarea {
    flex: 1;
    border: none;
    background: transparent;
    padding: 10px 40px 10px 10px;
    font-size: 15px;
    resize: none;
    max-height: 100px;
    outline: none;
    font-family: inherit;
    color: var(--text-primary);
}

.message-input-wrapper textarea::placeholder {
    color: var(--text-tertiary);
}

.input-emoji {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
}

.input-emoji .icon-btn {
    color: var(--text-tertiary);
}

.input-emoji .icon-btn:hover {
    color: var(--primary-color);
}

.send-btn {
    width: 46px;
    height: 46px;
    border-radius: var(--border-radius-circle);
    border: none;
    background: var(--primary-color);
    color: var(--text-light);
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    transition: all var(--transition-fast);
}

.send-btn:disabled {
    background: var(--text-tertiary);
    cursor: not-allowed;
    transform: none;
}

.send-btn:not(:disabled):hover {
    background: var(--primary-dark);
    transform: scale(1.05);
}

/* Адаптивность */
@media (max-width: 1024px) {
    .sidebar {
        width: 40%;
        min-width: 280px;
    }

    .message {
        max-width: 85%;
    }
}

@media (max-width: 768px) {
    .messenger-container {
        width: 100%;
        height: 100vh;
        border-radius: 0;
    }
}